<template>
    <div class="promote-poster-container column-center">
        <img class="poster" :src="imgUrl" />
        <div class="save-btn br60 md white row-center">
            长按图片保存到相册 
        </div>
    </div>
</template>

<script>
import {getInviteBanner} from "@API/user"
export default {
    name: "promotePoster",
    data() {
        return {
            imgUrl: ''
        }
    },
    methods: {
        $getInviteBanner() {
            getInviteBanner({
                url: "/mobile/index/home"
            }).then(res => {
                if(res.code == 1) {
                    this.imgUrl = res.data.url
                }
            })
        }
    },
    mounted() {
        this.$getInviteBanner()
    }
}
</script>

