        min-height: 100vh;
        padding: 0 37px;
        background: linear-gradient(180deg, #333333 0%, #767676 100%);
        .poster {
            width: 300px;
            height: 490px;
        }
        .save-btn {
            margin-top: 30px;
            background-color: $--color-primary;
            width: 100%;
            height: 41px;
        }
    }
